import { Button, Modal } from "antd";
import * as React from "react";
import "react-inner-image-zoom/lib/InnerImageZoom/styles.css";
import InnerImageZoom from "react-inner-image-zoom";
import {
  ArrowDownTrayIcon,
  ArrowsPointingOutIcon,
} from "@heroicons/react/24/outline";
import { truncateText } from "../../utils";

const GalleryView = ({ data }: any) => {
  const [isModalVisible, setIsModalVisible] = React.useState(false);
  const [selectedItem, setSelectedItem] = React.useState<number>(0);

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const downloadBase64File = (base64Data: string, fileName: string) => {
    const linkSource = base64Data;
    const downloadLink = document.createElement("a");
    downloadLink.href = linkSource;
    downloadLink.download = fileName;
    downloadLink.click();
    downloadLink.remove();
  };

  const gallery = data.images?.map((item: string, i: number) => {
    return (
      <div
        className="hover:scale-110   drop-shadow-lg hover:drop-shadow-xl hover:z-50 transition duration-300"
        key={"row" + i}
      >
        {/* <img
          className="rounded duration-300 hover:drop-shadow-md  hover:scale-110"
          src={item}
        />{" "} */}

        <div className="flex   bg-secondary p-2 rounded-t">
          <div className="flex-1 text-center"></div>
          <span
            role={"button"}
            onClick={() => {
              let fname = data.config.prompt.replace(/[^A-Z0-9]/gi, "_") + "_";
              fname = fname.substring(0, 100) + i + "_.png";
              downloadBase64File(item, fname);
            }}
            className="inline-block mr-2  "
          >
            <ArrowDownTrayIcon className="h-5  inline-block hover:text-accent transition duration-300" />
          </span>
          <span
            role={"button"}
            onClick={() => {
              setSelectedItem(i);
              setIsModalVisible(true);
            }}
            className="inline-block "
          >
            <ArrowsPointingOutIcon className="h-5 inline-block hover:text-accent   transition duration-300" />
          </span>
        </div>
        <InnerImageZoom
          zoomType="hover"
          zoomPreload={true}
          className="w-full rounded rounded-t-none duration-300 hover:z-50"
          src={item}
          hideHint={true}
        />
      </div>
    );
  });

  const selectedImage = data.images?.[selectedItem];

  return (
    <div className=" ">
      <Modal
        title={truncateText(data.config.prompt, 50)}
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        // width={"80%"}
        footer={[
          <Button
            onClick={() => {
              setIsModalVisible(false);
            }}
            key="close"
            className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
            type="primary"
          >
            close
          </Button>,
        ]}
      >
        {selectedImage && (
          <>
            <InnerImageZoom
              zoomType="hover"
              zoomPreload={true}
              className="w-full rounded"
              hideHint={true}
              src={selectedImage}
            />
          </>
        )}
      </Modal>
      <div className=" font-semibold text-primary py-2">
        Results
        <span className="text-xs ml-1 text-accent">{data.images.length}</span>
      </div>
      <div>
        <span className=" mb-3 inline-block text-xs text-secondary">
          {data.config.prompt}
        </span>
      </div>
      <div className="grid grid-cols-4 gap-3">{gallery}</div>
    </div>
  );
};
export default GalleryView;
