import {
  AdjustmentsVerticalIcon,
  Cog6ToothIcon,
} from "@heroicons/react/24/outline";
import { Button, Modal, Slider } from "antd";
import * as React from "react";
import { IGeneratorConfig } from "../../types";

const GeneratorControlsView = ({
  setConfig,
  config,
  generate,
  isLoading,
}: {
  setConfig: (config: IGeneratorConfig) => void;
  config: IGeneratorConfig;
  generate: (config: IGeneratorConfig) => void;
  isLoading: boolean;
}) => {
  const [isModalVisible, setIsModalVisible] = React.useState(false);

  //   const [generationConfig, setGenerationConfig] =
  //     React.useState<IGeneratorConfig>(config);

  const ControlRowView = ({ title, description, value, control }: any) => {
    return (
      <div>
        <div>
          <span className="text-primary inline-block">{title} </span>
          <span className="text-xs ml-1 text-accent -mt-2 inline-block">
            {value}
          </span>
        </div>
        <div className="text-secondary text-xs"> {description} </div>
        {control}
      </div>
    );
  };

  return (
    <div className="mt-4 text-secondary rounded p">
      <Modal
        title={
          <span>
            <AdjustmentsVerticalIcon className="h-4 text-accent inline-block mr-2 -mt-1" />
            Generation Settings
          </span>
        }
        visible={isModalVisible}
        onCancel={() => {
          setIsModalVisible(false);
        }}
        onOk={() => {
          setIsModalVisible(false);
        }}
        footer={[
          <Button
            onClick={() => {
              setIsModalVisible(false);
            }}
            key="close"
            className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
            type="primary"
          >
            close
          </Button>,
          !isLoading && (
            <Button
              onClick={() => {
                generate(config);
                setIsModalVisible(false);
              }}
              key="generate"
              className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
              type="primary"
            >
              close and generate
            </Button>
          ),
        ]}
      >
        <ControlRowView
          title="Width"
          description="Width of image to be generated. Multiples of 8."
          value={config.width}
          control={
            <Slider
              min={448}
              max={1024}
              defaultValue={config.width}
              step={64}
              onAfterChange={(value) => {
                setConfig({ ...config, width: value });
              }}
            />
          }
        />
        <ControlRowView
          title="Height"
          description="Height of image to be generated. Multiples of 8."
          value={config.height}
          control={
            <Slider
              min={448}
              max={1024}
              defaultValue={config.height}
              step={64}
              onAfterChange={(value) => {
                setConfig({ ...config, height: value });
              }}
            />
          }
        />
        <ControlRowView
          title="Number of Images"
          description="Number of images to be generated."
          value={config.num_images}
          control={
            <Slider
              min={1}
              max={15}
              defaultValue={config.num_images}
              step={1}
              onAfterChange={(value) => {
                setConfig({ ...config, num_images: value });
              }}
            />
          }
        />
        <ControlRowView
          title="Guidance Scale"
          description="Controls how closely the generation adhers to the guidance in the conditional signal (prompt, reference image). "
          value={config.guidance_scale}
          control={
            <Slider
              min={0}
              max={20}
              defaultValue={config.guidance_scale}
              step={0.1}
              onAfterChange={(value) => {
                setConfig({ ...config, guidance_scale: value });
              }}
            />
          }
        />

        <ControlRowView
          title="Inference Steps"
          description="Number of steps to run inference for each image."
          value={config.num_inference_steps}
          control={
            <Slider
              min={1}
              max={200}
              defaultValue={config.num_inference_steps}
              step={1}
              onAfterChange={(value) => {
                setConfig({ ...config, num_inference_steps: value });
              }}
            />
          }
        />

        <p className="mt-4 text-xs hover:text-accent text-secondary">
          {" "}
          Learn more about stable diffusion parameters{" "}
          <a
            className="border-b border-accent"
            target={"_blank"}
            rel={"noopener noreferrer"}
            href={"https://huggingface.co/blog/stable_diffusion"}
          >
            here
          </a>
          .
        </p>
      </Modal>

      <div className="py-2 text-secondary md:flex gap-3 grid">
        {" "}
        <div className="flex-1">
          {" "}
          <span className="text-primary font-semibold">
            {" "}
            Write a prompt and get a generated response.
          </span>
          <div className="opacity-80 text-xs">
            width: <span className="text-accent"> {config.width}</span>, height:
            <span className="text-accent"> {config.height}</span>, number of
            images: <span className="text-accent"> {config.num_images}</span>{" "}
            ...
          </div>
        </div>
        <div
          role={"button"}
          onClick={() => {
            setIsModalVisible(true);
          }}
          className=" bg-secondary mb-2 inline-block p-3    hover:brightness-90 hover:bg-secondary transition duration-300 rounded px-4"
        >
          <>
            <AdjustmentsVerticalIcon className="h-4 text-accent inline-block mr-2 -mt-1" />
            Generation Settings{" "}
          </>
        </div>
      </div>
    </div>
  );
};
export default GeneratorControlsView;
